export default {
    name: "template-msg",
    data() {
        return {
            list: [],
        };
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.loadData();
    },
    methods: {
        loadData() {
            this.$showLoading();
            this.$request({
                url: this.$api.template.template,
            }).then(response => {
                this.$hideLoading();
                if (response.code == 0) {
                    this.list = response.data.list;
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    })
                }
            }).catch(response => {
                this.$hideLoading();
            });
        },
        subscribe(index) {
            let template = this.list[index];
            this.$subscribe([template.tpl_id]).then(res => {

                if (res[template.tpl_id] == 'accept') {
                    uni.showModal({
                        title: '提示',
                        content: '订阅成功',
                        showCancel: false,
                    })
                } else {
                    uni.showModal({
                        title: '提示',
                        content: '取消订阅',
                        showCancel: false,
                    })
                }








            });
        },
        send(index) {
            this.$showLoading({
                text: '发送中'
            });
            this.$request({
                url: this.$api.template.template,
                method: 'post',
                data: {
                    templateTpl: this.list[index].tpl_name
                }
            }).then(response => {
                this.$hideLoading();
                if (response.code == 0) {
                    uni.showModal({
                        title: '提示',
                        content: response.data.msg,
                        showCancel: false,
                    })
                } else {
                    this.$hideLoading();
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    })
                }
            })
        }
    }
}
